import Typography from 'typography';

const typography = new Typography({
  baseFontSize: '16px',
  baseLineHeight: 1.6,
  bodyFontFamily: ['georgia', 'times', 'serif'],
  headerFontFamily: [
    '-apple-system',
    'BlinkMacSystemFont',
    'avenir next',
    'avenir',
    'helvetica',
    'helvetica neue',
    'ubuntu',
    'roboto',
    'noto',
    'segoe ui',
    'arial',
    'sans-serif',
  ],
});

export default typography;



